import { Component, Host, h, State, Element, Listen, Prop } from '@stencil/core';
import { deepQuerySelectorAll } from '../../../utils/deepQuerySelectorAll';

/**
 * The form container component.
 */
@Component({
  tag: 'bs-form-container',
  styleUrl: 'form-container.scss',
  shadow: false,
})
export class BsFormContainer {
  private allFields: HTMLBsFormFieldElement[];

  private acceptedRequestStatus = [200, 201];

  @Element() hostElement: HTMLBsFormContainerElement;

  /**
   * State
   */

  @State() formHasErrors: boolean = false;

  @State() isFormSending: boolean = false;

  @State() submitHasFailed: boolean = false;

  /**
   * Props
   */

  /**
   * The endpoint for the submit request.
   */

  @Prop() action: string;

  /**
   * Message to inform the user there are issues with the filled in values.
   */

  @Prop() invalidFormMessage: string;

  /**
   * Message to inform the usere something went wrong while submitting the form.
   */

  @Prop() submitErrorMessage: string;

  /**
   * The page the user is redirected to in case of a successfull submit.
   */

  @Prop() successRedirect: string;

  /**
   * Events
   */

  @Listen('bsButtonClick')
  async onBsButtonClickHandler(e: CustomEvent) {
    if (e.detail.type === 'submit') {
      this.formHasErrors = false;
      this.submitHasFailed = false;
      this.validateForm().then(() => {
        if (this.isFormValid() && !this.isFormSending) {
          this.isFormSending = true;
          this.submitRequest();
        }
      });
    }
  }

  /**
   * Methods
   */

  private async validateForm() {
    this.formHasErrors = false;
    await Promise.all(this.allFields.map((field) => field.validate()));
  }

  private isFormValid() {
    this.formHasErrors = this.allFields.map((field) => field.invalid).includes(true);

    return !this.formHasErrors;
  }

  private getData() {
    const formData = {};

    this.allFields.forEach((field) => {
      formData[field.name] = field.value;
    });

    return formData;
  }

  private async submitRequest() {
    try {
      const response = await fetch(this.action, {
        method: 'POST',
        body: JSON.stringify(this.getData()),
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (this.acceptedRequestStatus.includes(response.status)) {
        window.location.href = this.successRedirect;
      } else {
        this.submitHasFailed = true;
      }
    } catch {
      this.submitHasFailed = true;
    } finally {
      this.isFormSending = false;
    }
  }

  /**
   * Lifecycle hooks
   */

  componentDidLoad() {
    this.allFields = deepQuerySelectorAll(
      'bs-form-field',
      this.hostElement
    ) as HTMLBsFormFieldElement[];
  }

  /**
   * Render
   */

  render() {
    return (
      <Host>
        <form
          class={`form-container ${this.isFormSending ? 'is-sending' : ''}`}
          action={this.action}
          method="dialog"
        >
          <slot />
          {this.formHasErrors && <p class="form-error">{this.invalidFormMessage}&nbsp;</p>}
          {this.submitHasFailed && <p class="form-error">{this.submitErrorMessage}&nbsp;</p>}
        </form>
      </Host>
    );
  }
}
